@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Montserrat:wght@500&display=swap");

@font-face {
  font-family: "Bartkey";
  src: local("Bartket"), url("./fonts/BARTKEY.ttf") format("truetype");
  font-weight: normal;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

html {
  width: 100%;
  height: 100vh;
}

* {
  box-sizing: border-box;
}

img {
  width: 100%;
}

.bg {
  background: url(./assets/laptop/events_balcony.svg) no-repeat;
  background-size: cover;
  height: 100vh;
  z-index: -10;
  /* position: absolute;
  top: 0;
  left: 0; */
}

.container {
  width: 80%;
  position: absolute;
  top: 1rem;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  z-index: 0;
  height: calc(100% - 4rem);
  /* overflow-x: hidden;
  overflow-y: scroll;
  height: 90%; */
}

.heading {
  font-family: Bartkey;
  font-size: 4rem;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: center;
  overflow-x: hidden;
  overflow-y: scroll;
  height: calc(100% - 1.75rem)
}

.card {
  width: 33%;
  height: 215px;
  margin: 1rem 1rem;
  background: rgba(7, 9, 73, 0.09);
  box-shadow: inset 0px -106.879px 88.6312px -83.4176px rgba(104, 80, 148, 0.3),
    inset 0px 5.2136px 23.4612px rgba(107, 100, 154, 0.3),
    inset 0px 1.3034px 52.136px rgba(179, 171, 226, 0.2);
  backdrop-filter: blur(130px);
  border-radius: 20px;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-img {
  height: 100%;
}

.card-img img {
  width: unset;
  height: 100%;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.card-text {
  font-family: "Lato", sans-serif;
  font-family: "Montserrat", sans-serif;
  width: 60%;
  padding: 0 1rem;
}

.view-btn {
  width: 80%;
  cursor: pointer;
  padding: 0.5rem 1rem;
  background-color: rgba(255, 255, 255, 0.9);
  color: #000000;
  outline: 1px solid rgba(255, 255, 255, 0.9);
  text-align: center;
  outline-offset: 3px;
}

.arrow {
  position: absolute;
  top: 2rem;
  left: 2rem;
  z-index: 10;
  width: 2rem;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

@media screen and (max-width: 768px) {
  .container{
    top: 4rem;
  }

  .card-container {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: unset;
    overflow-y: scroll;
    width: 100%;
    height: 87%;
    margin-top: 1rem;
  }

  .card {
    width: 100%;
    height: unset;
    margin: 1rem 0;
  }

  .card h3 {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  .card-img {
    width: 50%;
  }

  .card-img img {
    height: unset;
    width: 100%;
  }

  .arrow {
    left: 1rem;
  }

  .view-btn {
    width: 100%;
  }

  .heading {
    font-size: 3rem;
  }
}

@media screen and (min-width: 2560px) {
  .card {
    height: unset;
    width: 31%;
    margin: 1rem 1.5rem;
  }

  .card-img {
    width: 40%;
  }

  .card-img img {
    height: unset;
    width: 100%;
  }

  .view-btn{
    width: 50%;
  }
}
