.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Landing CSS */
body {
  margin: 0;
  overflow: hidden;
  /* overflow-anchor: none; */
}

html {
  width: 100%;
  height: 100vh;
}

* {
  box-sizing: border-box;
}

img {
  width: 100%;
}

#wrapper{
  position: relative;
  width: 100%;
  overflow: hidden;
}

/* .sky img,
.logo img,
.stars img,
.balcony img,
.moon img {
  width: 100%;
} */

.sky {
  background: url(./assets/laptop/Sky.svg) no-repeat;
  background-size: cover;
  height: 100vh;
}

.stars {
  /* width: 100%;
  z-index: 5;
  position: absolute;
  top: 0;
  mix-blend-mode: screen; */
  background: url(./assets/laptop/Stars.png) no-repeat;
  background-size: cover;
  background-position: center center;
  height: 200vh;
  width: 200vw;
  z-index: 5;
  mix-blend-mode: screen;
  position: absolute;
  top: -50vh;
  bottom: 10vh;
  width: 100%;
  animation: rotateMoon 150s ease-in-out reverse infinite;
}

.balcony {
  width: 100%;
  z-index: 40;
  position: absolute;
  bottom: -5px;
  max-width: 100%;
}

.balcony-mobile {
  display: none;
}

.moon {
  width: 110%;
  z-index: 10;
  position: absolute;
  bottom: 10vh;
  left: 50%;
  transform: translateX(-50%);
  mix-blend-mode: screen;
  /* background: url(./assets/laptop/Moon.svg) no-repeat; */
  background-size: 100%;
  height: 110vh;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}
#moon {
  animation: rotateMoon 150s infinite;
  position: relative;
  top: 20%;
  /* mix-blend-mode: screen; */
}

@keyframes rotateMoon {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

.balcony .glow {
  width: 100%;
  background: radial-gradient(
    217.21% 217.2% at 50% 96.77%,
    #ffffff 0%,
    rgba(112, 36, 36, 0.41) 100%
  );
  mix-blend-mode: color-dodge;
  opacity: 0.4;
  filter: blur(20px);
  transform: matrix(1, 0, 0, -1, 0, 0);
  position: absolute;
  top: 0;
  z-index: 40;
  height: 65%;
}

.balcony .horizon-glow {
  width: 90%;
  height: 30%;
  position: absolute;
  bottom: calc((100vw * 220) / 1538 - 30px);
  left: 50%;
  transform: translateX(-50%);
  background: radial-gradient(
    50% 50% at 50% 50%,
    #f6feff 0%,
    #c3bfc5 41.46%,
    #573c4b 100%
  );
  background: blue;
  mix-blend-mode: color-burn;
  opacity: 0.65;
  filter: blur(60px);
  z-index: 10;
}

.balcony .horizon-glow-buildings {
  width: 70%;
  height: 30%;
  position: absolute;
  bottom: calc((100vw * 220) / 1538 - 30px);
  left: 50%;
  transform: translateX(-50%);
  background: radial-gradient(
    103.57% 103.57% at 50% 50%,
    #ffffff 0%,
    #180000 100%
  );
  background: blue;
  mix-blend-mode: color-dodge;
  opacity: 0.3;
  filter: blur(40px);
  z-index: 40;
}

.city {
  position: absolute;
  bottom: calc((100vw * 220) / 1538 - 6px);
  width: 50%;
  left: 50%;
  z-index: 35;
}

.city1 {
  width: 70%;
  transform: translate(-45%, 100%);
  z-index: 30;
  transition: transform 0.8s ease-out;
}

.city2 {
  width: 65%;
  transform: translate(-45%, 100%);
  z-index: 25;
  transition: transform 0.8s ease-out;
  transition-delay: 0.5s;
}

.city3 {
  width: 50%;
  transform: translate(-50%, 100%);
  z-index: 20;
  transition: transform 1s ease-out;
  transition-delay: 1s;
}

.city4 {
  width: 46%;
  transform: translate(-53%, 100%);
  z-index: 17;
  transition: transform 1.2s ease-out;
  transition-delay: 1.7s;
}

.city5 {
  width: 30%;
  transform: translate(-75%, 100%);
  z-index: 15;
  transition: transform 1.3s ease-out;
  transition-delay: 2.3s;
}

.city6 {
  width: 19%;
  transform: translate(-2%, 100%);
  z-index: 12;
  transition: transform 1.3s ease-out;
  transition-delay: 2.3s;
}

.socials {
  position: absolute;
  bottom: 0rem;
  left: 1.5rem;
  z-index: 50;
}

.socials div {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-bottom: 1.25rem;
  cursor: pointer;
}

.sign{
  position: absolute;
  width: 12rem;
  bottom: 0;
  left: 7rem;
  z-index: 50;
  /* bottom: calc((100vw * 220) / 1538 - 6px); */
  bottom: 6rem;
  mix-blend-mode: screen;
}

@media screen and (max-width: 768px) {
  .balcony-lp {
    display: none;
  }

  .balcony-mobile {
    display: block;
  }

  .sky {
    background: url(./assets/mobile/Sky.svg) no-repeat;
    background-size: 100% 100%;
    height: 100vh;
  }

  .stars {
    background: url(./assets/mobile/Stars.png) no-repeat;
    background-size: 100% 100%;
    height: 100vh;
    top: -20vh;
    animation: rotateMoon 150s ease-in-out reverse infinite;
  }

  .moon {
    /* background: url(./assets/mobile/Moon.svg) no-repeat; */
    left: 50%;
    width: 100%;
    background-size: 100%;
    bottom: 1.75rem;
    mix-blend-mode: screen;
  }
  #moon {
    /* display: none; */
    width: 400vw;
  }

  .city {
    bottom: calc((100vw * 300) / 414 - 7px);
  }

  .city1{
    width: 241%;
  }

  .city2{
    width: 161%;
  }

  .city3{
    width: 141%;
  }
  
  .city4{
    left: calc(50% - 1rem);
    width: 132%;
    bottom: calc((100vw * 300) / 414 - 1rem);

  }

  .city5{
    width: 90%;
    bottom: calc((100vw * 300) / 414 - 2rem);
  }

  .city6{
    width: 50%;
    left: calc(50% - 1rem);
  }

  .balcony .glow{
    height: 35%;
  }

  .sign{
    left: 0;
    bottom: 12rem;
    /* width: 8rem; */
  }
}

/* NavBar CSS */

nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  position: absolute;
  top: 0;
  z-index: 20;
}

.hamburger {
  width: 35px;
  height: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  transition: transform 300ms ease-out;
}

.hamburger.open {
  transform: rotate(-45deg);
}

.line-menu {
  background-color: #fff;
  border-radius: 5px;
  width: 100%;
  height: 4px;
}

.line-menu.half {
  width: 50%;
}

.line-menu.start {
  transition: transform 300ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transform-origin: right;
}

.open .line-menu.start {
  transform: rotate(-90deg) translateX(3px);
}

.line-menu.end {
  align-self: flex-end;
  transition: transform 300ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transform-origin: left;
}

.open .line-menu.end {
  transform: rotate(-90deg) translateX(-3px);
}

.logo {
  width: 18vw;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  nav{
    flex-direction: column;
    padding: 1.5rem;
  }

  nav div:last-child{
    text-align: right;
    align-self: flex-end;
  }

  .logo{
    order: 2;
    width: 80%;
    margin-top: 3rem;
  }

  .hamburger{
    order: 1;
    justify-items: flex-end;
    text-align: right;
    width: 30px;
    height: 25px;
  }

  .line-menu{
    height: 3px;
  }
}
@media screen and (max-width: 376px) {
  #moon {
    top:40%;
  }
}